'use client'

import { Container, Grid, Card, CardContent, Typography, Button } from '@mui/material';
import { useGetBlogListQuery } from "@/services";
import Link from 'next/link';

export const Blog = ( ) =>{
    const { data, error, isLoading } = useGetBlogListQuery();

    if (isLoading) return <Loader/>;
    if (error) return <div>Error loading posts!</div>;

  return(
    <Container>
    <Grid container spacing={2}>
      {Array.isArray(data) && data.length > 0 && data?.map((post) => (    
        <Grid item size={{ xs: 12, sm: 4, md: 4 }} key={post.id}>
          <Card sx={{width:'100%'}}>
            <CardContent>
              <Typography variant="h6" gutterBottom>{post.title}</Typography>
              <Typography variant="subtitle2" paragraph>{post?.author}</Typography>
              <Typography variant="body2" color='text.secondary' paragraph>{post?.excerpt}</Typography>
              <Button
                component={Link}
                href={{
                    pathname: '/blogDetail',
                    query: { id: post.id },
                  }}
                variant="contained"
                color="primary"
              >
                Read More
              </Button>
            </CardContent>
          </Card>
        </Grid>
      ))}
    </Grid>
  </Container>
  )
}